.main-slider-block {
	margin-bottom: 57px;
	@include r(991) {
		margin-bottom: 43px; }
	@include r(767) {
		margin-bottom: 51px; } }
.main-slider {
	overflow: hidden; }
.main-slider__slide {
	@include css-lock(400, 670, 991, 1599, "min-height");
	@include cover;
	position: relative;
	@include r(639) {
		min-height: 250px; } }
.main-slider__content {
	text-align: center;
	display: table;
	@include css-lock(400, 670, 991, 1599, "min-height");
	width: 100%;
	@include r(639) {
		min-height: 250px; } }
.main-slider__h2 {
	display: table-cell;
	vertical-align: middle;
	color: $white;
	font-family: $PBK;
	font-weight: normal;
	line-height: 1.3em;
	text-transform: uppercase;
	@include css-lock(52, 95, 991, 1599);
	@include r(991) {
		line-height: 0.6em; }
	@include r(639) {
		font-size: 24px; }
	span {
		position: relative;
		font-family: $PT;
		@include css-lock(35, 65, 991, 1599);
		@include css-lock(12, -42, 991, 1599, "top");
		@include r(639) {
			font-size: 20px; } } }
.slick-prev, .slick-next {
	top: auto;
	bottom: 30px;
	z-index: 1;
	@include r(991) {
		bottom: 27px; } }
.slick-prev {
	@include s(prev);
	left: 50%;
	margin-left: -40px; }
.slick-next {
	@include s(next);
	right: 50%;
	margin-right: -40px; }
